@import "@/sass/variables.scss";





























































































































































.homeCom{
  width: 100%;
  height: 100%;
  &_content{
    width: 100%;
    height: 100%;
    // background-color: #d1d1d1;
    @include articlePadding;
  }
  &_body{
    width: 100%;
    height: 100%;
    overflow: auto;
  }
  &_font{
    padding-bottom: 10px;
    border-bottom: 1px solid #000;
    h1{
      font-size: 24px;
      padding: 6px 0;
    }
    .font_detail{
      text-indent:2em;
    }
  }
}
