@import "@/sass/variables.scss";

































































.ReturnTopcom{
	width: 50px;
	height: 50px;
	position: fixed;
	right: 50px;
	bottom: 50px;
	.ReturnTopcom_content{
		width: 100%;
		height: 100%;
		border-radius: 50%;
		border-color: #666;
		background-color: rgba(153,153,153,0.2);
		@include commonShadow;
		cursor: pointer;
		.top_img{
			width: 100%;
			height: 100%;
		}
	}
}

