@import "@/sass/variables.scss";



































































































































































































































































































	.editCom{
		width: 100%;
		@include articlePadding;
		.edit_search{
			width: 100%;
			padding: 10px;
			.el-select{
				width: calc(100% - 100px);
    		margin-right: 20px;
			}
		}
		.articles_panel{
			padding: 10px;
			.article_data{
				display: flex;
				justify-content: space-between;
				align-items: center;
				.article_title{
					padding: 6px 0;
				}
				.operation{
					display: flex;
					align-items: center;
					.upload_op{
						display: inline-block;
						margin-right: 10px;
					}
				}
			}
		}
	}

