@import "@/sass/variables.scss";
















































































































































.articleCom{
	width: 100%;
	position: relative;
	@include articlePadding;
	.articleCom_content{
		width: 100%;
		.article_module{
			width: 100%;
			display: flex;
			flex-direction: column;
			margin-bottom: 30px;
			.module_title{
				font-weight: bold;
				font-size: 28px;
			}
			.article_list{
				@include articleFontStyle;
				display: flex;
				align-items: center;
				justify-content: space-between;
				.article_title{
					margin: 2px 0;
				}
				.title_radius{
					display: inline-block;
					width: 10px;
					height: 10px;
					border-radius: 50%;
					background-color: #000;
					margin: 0 10px;
				}
				.title_text{
					text-decoration: underline;
					color: #000;
					cursor: pointer;
				}
			}
		}
		.returnBtn{
			position: absolute;
			top: 10px;
			right: 0;
		}
	}
}

