@import "@/sass/variables.scss";






















































































































































.writeCom{
	width: 100%;
	@include articlePadding;
  .write_content{
		width: 100%;
		.article_module{
			width: 100%;
			.article_title,
			.article_file{
				height: 50px;
				display: flex;
				align-items: center;
				font-size: 16px;
				margin: 6px 0;
			}
			.title_text,
			.file_text{
				width: 120px;
				margin-right: 10px;
			}
			.title_input,
			.file_input{
				flex: 1;
			}
			.article_upload{
				margin: 10px 0;
				.upload_file{
					padding: 10px;
				}
			}
			.article_setting{
				display: flex;
				justify-content: center;
			}
		}
	}
}

